import * as React from 'react';

import DarkModeRoundedIcon from '@mui/icons-material/DarkModeRounded';
import LightModeRoundedIcon from '@mui/icons-material/LightModeRounded';
import darkLogo from '../assets/icons/logo_dark.svg'
import lightLogo from '../assets/icons/logo_light.svg'
import {Outlet, useNavigate} from "react-router-dom";
import {
    Avatar,
    Box,
    createTheme,
    CssBaseline,
    GlobalStyles,
    Grid,
    IconButton,
    Tab,
    Tabs,
    ThemeProvider,
    Typography,
    useTheme
} from "@mui/material";

const ColorModeContext = React.createContext({
    toggleColorMode: () => {
    }
});

function ToggleModeIcon() {
    const theme = useTheme();
    const colorMode = React.useContext(ColorModeContext);
    return (
        <Box
            sx={{
                display: 'flex',
                width: '30px',
                alignItems: 'center',
                justifyContent: 'center',
                bgcolor: 'transparent',
                color: 'text.primary',
                borderRadius: 1,
                p: 3,
            }}
        >
            <IconButton sx={{ml: 1}} onClick={colorMode.toggleColorMode} color="inherit">
                {theme.palette.mode === 'dark' ? <DarkModeRoundedIcon/> : <LightModeRoundedIcon/>}
            </IconButton>
        </Box>
    );
}


function LinkTab(props) {
    return (
        <Tab
            component="a"
            onClick={(event) => {
                event.preventDefault();
            }}
            sx={{
                width: '100px',
                borderRadius: 'sm',
                textTransform: 'lowerCase',
                fontWeight: '500'
            }}
            {...props}
        />
    );
}

export default function Routes() {
    const [mode, setMode] = React.useState('dark');
    const colorMode = React.useMemo(
        () => ({
            toggleColorMode: () => {
                setMode((prevMode) => (prevMode === 'light' ? 'dark' : 'light'));
            },
        }),
        [],
    );

    const theme = React.useMemo(
        () =>
            createTheme({
                palette: {
                    mode,
                },
            }),
        [mode],
    );

    const navigate = useNavigate();
    const [value, setValue] = React.useState('about');
    const navigationRoutes = {
        about: '/',
        work: '/work',
        blogs: '/blogs'
    }
    const handleChange = (event, newValue) => {
        setValue(newValue);
        navigate(navigationRoutes[newValue])
    };
    return (
        <ColorModeContext.Provider value={colorMode}>
            <ThemeProvider theme={theme}>
                <CssBaseline/>
                <GlobalStyles
                    styles={{
                        ':root': {
                            '--Collapsed-breakpoint': '769px', // form will stretch when viewport is below `769px`
                            '--Cover-width': '40vw', // must be `vw` only
                            '--Form-maxWidth': '100%',
                            '--Transition-duration': '0.4s', // set to `none` to disable transition
                        },
                    }}
                />
                <Box
                    sx={(theme) => ({
                        width: '100%',
                        transition: 'width var(--Transition-duration)',
                        transitionDelay: 'calc(var(--Transition-duration) + 0.1s)',
                        position: 'relative',
                        zIndex: 1,
                        display: 'flex',
                        justifyContent: 'center',
                        backdropFilter: 'blur(4px)',
                        backgroundColor: mode === 'light' ? 'rgba(255 255 255 / 0.6)' : 'rgba(0 0 0 / 0.6)',
                    })}
                >
                    <Box
                        sx={{
                            display: 'flex',
                            flexDirection: 'column',
                            minHeight: '100vh',
                            width: '100%',
                            maxWidth: '100%',
                            px: 2,
                        }}
                    >
                        <Box
                            component="header"
                            sx={{
                                py: '10px',
                                px: '5px',
                                display: 'flex',
                                alignItems: 'center',
                                justifyContent: 'space-between',
                            }}
                        >
                            <Avatar
                                alt="Adarsh K."
                                src={mode === 'light' ? lightLogo : darkLogo}
                                sx={(theme) => ({
                                    width: 120,
                                    height: 120,
                                    marginRight: 1,
                                    backgroundSize: 'cover',
                                    backgroundPosition: 'center',
                                    backgroundRepeat: 'no-repeat',
                                })}
                            />
                            <ToggleModeIcon/>
                        </Box>
                        <Box component={"main"} sx={{
                            my: '0',
                            py: 2,
                            pb: 5,
                            mx: 'auto',
                        }}>
                            <Grid container spacing={2} sx={{flexGrow: 1, padding: ''}}>
                                <Grid xs={3}>
                                    <Box
                                        component="aside"
                                        sx={{
                                            py: 2,
                                            pb: 5,
                                            display: 'flex',
                                            flexDirection: 'column',
                                            justifyContent: 'flex-end',
                                            gap: 2,
                                            borderRadius: 'sm',
                                        }}
                                    >
                                        <Tabs
                                            value={value}
                                            onChange={handleChange}
                                            aria-label="nav tabs"
                                            orientation={"vertical"}
                                            sx={{
                                                width: '100px',
                                                borderRadius: 'sm',
                                                textTransform: 'lowerCase',
                                                fontWeight: '500',
                                                '& .MuiTabs-indicator': {display: 'none'}
                                            }}>
                                            <LinkTab label="about" value={'about'} href="/"/>
                                            <LinkTab label="work" value={'work'} href="/work"/>
                                           {/* <LinkTab label="blogs" value={'blogs'} href="/spam"/>*/}
                                        </Tabs>
                                    </Box>
                                </Grid>
                                <Grid xs={8}>
                                    <Outlet/>
                                </Grid>
                            </Grid>
                        </Box>
                        <Box component="footer" sx={{py: 3, position: 'fixed', bottom: 0}}>
                            <Typography level="body3" textAlign="center">
                                 adarshk.dev {new Date().getFullYear()}
                            </Typography>
                        </Box>
                    </Box>
                </Box>
            </ThemeProvider>
        </ColorModeContext.Provider>
    );
}
